
.detail{
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    &__text {
        margin-top: 22px;
        color: #4D4D4D;
    }

    &__title {
        font-weight: bold;
        font-size: 32px;
        line-height: 37px;
    }
    &__main {
        margin-top: 12px;
        padding: 0 10px;
        height: 40px;
        width: 100%;
        display:flex;
        align-items:center;
        background: #F2F2F2;
        border-radius: 4px;
        border: none;
    }
    &__button{
        margin-top:34px;
        width: 180px;
        border: 1px solid #0071BC;
        &:hover{
            background-color: #0071BC;
        }
    }
}
